.timerBox {
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.1);
  padding: 13px;
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.5);
  width: min-content;
}
.timerTitle {
  text-transform: uppercase;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0.8px;
  color: rgba(255, 255, 255, 0.5);
  font-weight: bold;
  margin: 0px;
  margin-bottom: 5px;
}
.timeText {
  margin: 0px;
  line-height: "100%";
}
.timer {
  display: flex;
  & > div {
    color: #ffffff;
    font-size: 23px;
    font-weight: bold;
    line-height: 28px;
    letter-spacing: 1.65px;
    & > span {
      font-size: 12px;
      line-height: 15px;
      letter-spacing: 0.85px;
      opacity: 0.4;
      margin: 0px 5px;
    }
  }
}
