.container {
  padding: 12px;
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  font-weight: bold;
  text-transform: uppercase;
}
.title {
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0.85px;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 5px;
}
.textWrapper {
  display: flex;
  gap: 5px;
  align-items: flex-end;
}
.text {
  font-size: 22px;
  letter-spacing: 1.64px;
  color: #ffffff;
}
.coin {
  font-size: 12px;
  letter-spacing: 0.85px;
  color: rgba(255, 255, 255, 0.4);
}
