.root {
  position: absolute;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  user-select: none;
  right: 0;
  padding: 0px 5px;
}
.selectBox {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  width: 250px;
  justify-content: center;
  @media (max-width: 500px) {
    justify-content: flex-end;
  }
}
.optionsList {
  background: #221339;
  position: absolute;
  border-radius: 8;
  padding: 5;
  right: 0;
  top: 30;
  width: min-content;
  box-shadow: -3px -3px 15px 10px rgba(60, 36, 102, 0.4);
}
.coinImg {
  width: 24px;
  height: 24px;
}
.optionItem {
  min-width: 240px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 2px;
  padding: 5px;
  border: 1px solid transparent;
  border-radius: 8px;
  &:hover {
    border: 1px solid rgba(72, 53, 234, 0.6);
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.0620629) 0%,
      rgba(255, 255, 255, 0.0755026) 100%
    );
  }
}
.checkbox {
  width: 18px;
  height: 18px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-radius: 3px;
  & img {
    display: none;
  }
}
.checkboxChecked {
  border: none;
  & img {
    display: block;
  }
}
.arrow {
  transform: rotate(180deg);
}
.arrowRotate {
  transform: rotate(0deg);
}
