.tableContainer {
  height: 100%;
  padding: 0;
}

.tHead {
  & th {
    font-family: "Gilroy";
    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 1px;
    color: rgba(159, 141, 208, 0.5);
    text-transform: uppercase;
    @media (max-width: 500px) {
      font-size: 10px;
    }
    &:first-child {
      text-align: start;
    }
    &:last-child {
      text-align: end;
    }
  }
}

.tHeaderCell {
  border-bottom: none;
  padding: 12px 0;
  width: 33%;
}

.tCell {
  border-bottom: 1px solid #2e2b4a;
  padding: 12px 0;
  font-family: "Gilroy";
  font-weight: 500;
  font-size: 18px;
  line-height: 19px;
  color: #ffffff;
  &:last-child {
    text-align: end;
  }
  @media (max-width: 500px) {
    font-size: 12px;
    padding: 8px 0px;
  }
}

.tCoinWrapper {
  display: flex;
  align-items: center;
  gap: 13px;
  font-weight: 500;
  font-size: 18px;
  line-height: 19px;
  color: white;
  & > img {
    width: 38px;
    height: 38px;
    @media (max-width: 500px) {
      width: 24px;
      height: 24px;
    }
  }
  @media (max-width: 500px) {
    font-size: 12px;
    gap: 5px;
  }
}

.tableTitle {
  font-size: 22px;
  line-height: 30px;
  letter-spacing: -1.15px;
  color: #ffffff;
  margin-bottom: 8px;
  text-transform: uppercase;
}

.note {
  display: flex;
  justify-content: flex-end;
  margin-top: 5px;
  color: rgba(255, 255, 255, 0.5);
  letter-spacing: 0.7px;
}

.buttonWrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 15px;
}

.button {
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 1px;
  color: #ffffff;
  text-transform: uppercase;
  background: transparent;
  border-radius: 8px;
  border: 2px solid #4835ea;
  padding: 10px 50px;
  &:hover {
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.0620629) 0%,
      rgba(255, 255, 255, 0.0755026) 100%
    );
  }
  &:focus {
    background: transparent;
  }
  & * {
    transition: transform 1s;
  }
  & *:hover {
    transform: scale(1.075);
  }
}
