.root {
  display: flex;
  flex-direction: column;
}

.headerContainer {
  padding: 30px 40px;
  color: #aca9f7;
  font-family: "Varela Round";
  font-size: 22px;
  line-height: 33px;
  letter-spacing: -1px;
  background: rgba(61, 56, 182, 0.4);
  display: flex;
  margin-bottom: 13px;
  border-radius: 8px;
  flex-direction: column;
  @media (max-width: 600px) {
    padding: 15px 20px;
    font-size: 16px;
    line-height: 20px;
  }
}

.headerTitle {
  display: flex;
  gap: 20px;
}

.container {
  width: 100%;
  background: #211338;
  border-radius: 8px 8px 0px 0px;
  padding: 25px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  @media (max-width: 500px) {
    padding: 10px;
  }
}

.feyorraLogo {
  width: 50px;
  height: 50px;
  align-self: center;
  @media (max-width: 500px) {
    width: 32px;
    height: 32px;
  }
}

.header {
  font-family: "Gilroy";
  font-size: 33px;
  line-height: 40px;
  letter-spacing: -1.5px;
  color: #ffffff;
  margin: 10px 0;
  @media (max-width: 500px) {
    font-size: 22px;
  }
}
