.tooltip {
  opacity: 0;
  position: absolute;
  width: 120px;
  background: linear-gradient(
    180deg,
    rgba(51, 47, 103, 0.35) 2.1%,
    rgba(69, 65, 133, 0.4) 57.45%,
    rgba(61, 60, 125, 0.6) 98.26%
  );
  box-shadow: 0px -30.4995px 48.7992px rgba(26, 24, 53, 0.506567);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  border-radius: 8px;
  pointer-events: none;
  @media (max-width: 500px) {
    width: 60px;
  }
}
.line {
  z-index: 2;
  height: 100%;
  width: 2px;
  background: linear-gradient(
    180deg,
    rgba(249, 76, 213, 0) 10%,
    rgba(250, 89, 255, 1) 50%,
    rgba(253, 138, 216, 0) 90%
  );
}
.tooltipTop {
  position: absolute;
  width: 100%;
  top: 0;
  backdrop-filter: blur(18px);
  border-radius: 8px 8px 0 0;
}
.tooltipBottom {
  position: absolute;
  width: 100%;
  bottom: 0;
  backdrop-filter: blur(18px);
  border-radius: 0 0 8px 8px;
}
.tooltipLabel {
  position: absolute;
  top: 0;
  z-index: 3;
  width: 100px;
  background: linear-gradient(
    180deg,
    rgba(51, 47, 103, 0.443218) 2.1%,
    rgba(61, 60, 125, 0.401268) 98.26%
  );
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3px;
  color: #ceceea;
  font-size: 15px;
  @media (max-width: 500px) {
    font-size: 10px;
    width: 80px;
    border-radius: 4px;
    gap: 1px;
  }
}
