@mixin alignTableRows {
  &:first-child {
    text-align: start;
  }
  &:last-child {
    text-align: end;
  }
}

.root {
  height: 100%;
}
.tableContainer {
  max-height: 300px;
  padding-right: 5px;
  overflow-y: auto;
}
.header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}
.tHead {
  & th {
    @include alignTableRows;
    font-family: "Gilroy";
    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 1px;
    color: rgba(159, 141, 208, 0.5);
    text-transform: uppercase;
    @media (max-width: 500px) {
      font-size: 10px;
    }
  }
}
.tHeaderCell {
  border-bottom: none;
  padding: 12px 0;
  width: 20%;
}
.tCell {
  @include alignTableRows;
  border-bottom: 1px solid #2e2b4a;
  padding: 12px 0;
  font-family: "Gilroy";
  font-weight: normal;
  font-size: 18px;
  line-height: 19px;
  color: #ffffff;
  @media (max-width: 500px) {
    padding: 8px 0px;
    font-size: 12px;
  }
}
.tCoinWrapper {
  display: flex;
  align-items: center;
  gap: 13px;
  font-weight: 500;
  font-size: 18px;
  line-height: 19px;
  & img {
    width: 38px;
    height: 38px;
    @media (max-width: 500px) {
      width: 24px;
      height: 24px;
    }
  }
  @media (max-width: 500px) {
    font-size: 14px;
    gap: 5px;
  }
}
.tableTitle {
  font-size: 22px;
  line-height: 30px;
  letter-spacing: -1.5px;
  color: #ffffff;
  margin-bottom: 8px;
  text-transform: uppercase;
}
