.metaMaskContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 20px;
  @media (max-width: 500px) {
    gap: 10px;
  }
}

.metaMaskButton {
  cursor: pointer;
  display: flex;
  align-items: center;
  color: #090c16;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 20px;
  gap: 16px;
  line-height: 24px;
  padding: 11px 24px 13px 19px;
  background: #ffffff;
  border-radius: 8px;
  border: none;

  @media (max-width: 500px) {
    font-size: 14px;
    line-height: 16px;
    gap: 8px;
    padding: 5px 12px 6px 10px;
    & img {
      width: 24px;
      height: 24px;
    }
  }
}
.loader {
  width: 38px !important;
  height: 38px !important;
  @media (max-width: 500px) {
    width: 24px !important;
    height: 24px !important;
  }
}

.addressWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.address {
  font-size: 14px;
  flex-direction: column;
  align-items: flex-start;
  @media (max-width: 500px) {
    font-size: 8px;
  }
}

.dataWrapper {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  @media (max-width: 500px) {
    gap: 10px;
  }
}

.stakeWrapper {
  font-family: Varela Round;
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  line-height: 19px;
  flex-direction: column;
  color: rgba(255, 255, 255, 0.5);
}
